.card {
    border: 2px solid skyblue;
    border-radius: 2px;
    width: 200px;
    height: 200px;
    margin: 1rem;
    position: relative;
    -webkit-box-shadow: 6px 6px 12px rgb(56, 56, 56);
            box-shadow: 6px 6px 12px rgb(56, 56, 56);
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }
  
  .card > .img-fluid {
    height: 100%;
    overflow: hidden;
    background:rgb(255, 255, 255);
  }
  
  .card > .img-fluid > img {
    height: 100%;
    width: 100%; 
  }

  .card > .img-fluid > img:hover {
    border: 2px solid lightseagreen;
  }
  
  .card:hover {
    -webkit-box-shadow: 7px 7px 9px 8px rgb(56, 56, 56);
            box-shadow: 7px 7px 9px 8px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    border-radius: 10%; 
    border-style: none;  
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
        -ms-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }


